import test from 'ava';

// import * as Client from './client.js';

test.only('afficher / masquer le texte', t => {
  document.body.innerHTML = '<script src="client.js"></script><div id="seeMoreDiv" style="display:none">Des explications très intéressantes</div><button id="seeMoreButton" onclick="Client.seeMore()">Voir plus</button>';
  setTimeout(() => {
    document.getElementById('seeMoreButton').click();
    // Client.seeMore();
    t.is(document.getElementById('seeMoreDiv').style.display, 'block');
    document.getElementById('seeMoreButton').click();
    // Client.seeMore();
    t.is(document.getElementById('seeMoreDiv').style.display, 'none');
  }, 1000);
});
